import React from 'react';
import pic from '../src/image/about1.jpg';
const About=()=> {
    return (
     <>
     <div className="container-fluid nav_bg"> 
        <div className="row">
           <div className='col-10 mx-auto'> 
           
     <h1 className='mt-3 text-center brand-name'>About Us</h1>
     <hr className='mb-4'/>
     <h1 className='text-center'>Vision</h1>
     <div className="row mx-auto ml-2 justify-content-center d-flex">
              <div className='col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column'>
              <p className=' image  my-3 text.center col-sm-8 solmd-8 mx-auto justify-content-center'>
        The company's vision is to empower millions of service professionals across the world to deliver services at Home.
        Our first priority to Save Your Time and Money. and we want to provide  
        best services by the Experienced Worker in sort time.  </p>
              </div>
              <div className="col-lg-3 my-3 order1 order-lg-2 header-img">
           <img  src={pic} className='img-fluid animated image' alt='brand pic' /> 
        </div>
              </div>
              
            
              <h1 className='mt-4 text-center '>Who we are</h1>
            <p className='image  my-3 text.center col-sm-8 solmd-8 mx-auto justify-content-center'>PechKas helps customers book reliable home services like plumbing, electrician, painting etc.</p>
              
            <h1 className='mt-4 text-center '>How we do it</h1>
            <p className='image  my-3 text.center col-sm-8 solmd-8 mx-auto justify-content-center'>PechKas provides a plateform that allows skilled and experienced professionals to connect with users looking for specific services. All the professionals, though experienced and skilled, undergo intensive training modules before being allowed to list their services on the plateform</p>
     </div>
     </div>
     </div>
     </> 
    );
  }
  
  export default About;
  