import React from 'react';
import { NavLink } from 'react-router-dom';
import lp from '../src/image/logp.jpg';
const Register=()=> {
    return (
     <>
     <section id='header' className="d-flex align-items-center">
        <div className="container-fluid nav_bg">
          <div className="row">
            <div className='col-10 mx-auto'>
            <div className="row">
            <div className="col-lg-6 order1 order-lg-2 header-img">
            <h2 className=' mx-5 text-center brand-name'>
               Login as Professional
              </h2>
              <hr className='mt-3'></hr>
              <form className=' mx-3'>
  <div class="mb-3 ">
    <label for="exampleInputEmail1" class="image form-label">Email address</label>
    <input type="email" class="image form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Username'/>
  </div>
  <div class="mb-3">
    <label for="exampleInputPassword1" class="image form-label">Password</label>
    <input type="password" class="image form-control" id="exampleInputPassword1" placeholder='Your Password'/>
    {/* <div id="emailHelp" class="image form-text">We'll never share your password with anyone else.</div> */}
  </div>
  {/* <div class="mb-3 form-check">
    <input type="checkbox" class="form-check-input" id="exampleCheck1"/>
    <label class="form-check-label" for="exampleCheck1">Check me out</label>
  </div> */}
  <NavLink to='/'><button type="submit" class="btn btn-primary">Login</button></NavLink>
  <p className='mt-2 ml-3 copyright'>Don't have an account?<NavLink to='/RegisterPro'>Apply to PechKas</NavLink>
</p>
</form>
        </div>
              <div className='col-md-5  pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column'>
              
              <img src={lp} className='img-fluid animated logimg' alt='brand pic'/> 
              
             
              </div>
        
        </div>
            </div>
          </div>
        </div>
      </section>
      
      </>
    );
  }
  
  export default Register;
  