import React from 'react';
import { NavLink } from 'react-router-dom';
import plumber from '../src/image/plumber.jpg'
import electric from '../src/image/electrician.jpg';
import barbar from '../src/image/salon.jpg';
const Service=()=> {
    return (
     <>
     <h1 className='mt-3 brand-name text-center'>Our Services</h1>
     
      <div className=" mt-3 col-sm-6 container-fluid nav_bg"> 
      <hr className='mt-3'/>
        <div className="row">
           <div className='col-5 mx-auto'> 
           
          <div className="card">
  <img src={plumber} className=" col-md-2 card-img-top" alt="..."/>
  <div class=" mt-2 card-body">
    <h5 class=" mt-1 card-title image brand-name">Plumbing service</h5>
    <p class="card-text image">Book Skilled and Experienced Professionals</p>
    <NavLink to="/" class="btn btn-primary">Check Now</NavLink>
  </div>
</div>
     </div>
     <div className='col-5 mx-auto'> 
          <div className="card">
  <img src={electric} className="mx-auto col-md-2 card-img-top" alt="..."/>
  <div className=" mt-2 card-body">
    <h5 className="image mt-1 card-title brand-name">Electrician</h5>
    <p  className="image card-text">Book Skilled and Experienced Professionals</p>
    <NavLink to="/" className="btn btn-primary image">Check Now</NavLink>
  </div>
</div>
     </div>
     <div className='mt-3 col-5 mx-auto'> 
          <div className="card">
  <img src={barbar} className="mx-auto col-md-2 card-img-top" alt="..."/>
  <div className=" mt-2 my-2 card-body">
    <h5 className=" mt-1 card-title image brand-name">Carpenter</h5>
    <p className="card-text image">Book Skilled and Experienced Professionals</p>
    <NavLink to="/" className="btn btn-primary">Check Now</NavLink>
  </div>
</div>
     </div>
     </div>
     </div>
     </> 
    );
  }
  
  export default Service;
  