import React from 'react';
import { NavLink } from 'react-router-dom';
import pic from '../src/image/img1.jpg';
const Home = () => {
  return (
    <>
      <section id='header' className="d-flex align-items-center">
        <div className="container-fluid nav_bg">
          <div className="row">
            <div className='col-10 mx-auto'>
            <div className="row">
              <div className='col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column'>
              <h1>Save your Time and Money With
                <strong className='brand-name'> PechKas</strong>
              </h1>
              <h2 className='my-3'>
              We are team of Experienced worker serving at Home
              </h2>
              <div className='mt-3'>
                <NavLink  to="/service" className='btn-get-started'>get Started</NavLink>
              </div>
              </div>
        <div className="col-lg-6 order1 order-lg-2 header-img">
           <img src={pic} className='img-fluid animated' alt='brand pic'/> 
        </div>
        </div>
            </div>
          </div>
        </div>
      </section>

    </>
  );
}

export default Home;




