import React from 'react';
import { NavLink } from 'react-router-dom';
import logo from '../src/image/logo.png'
// import {NavLink} from 'react-router-dom';
const Navbar = () => {
  return (
    <>
       <div className="container-fluid nav_bg"> 
        <div className="row">
           <div className='col-10 mx-auto'> 

            <nav className="navbar navbar-expand-lg navbar-light bg-light">
              
              <NavLink className="navbar-brand" to="/" ><img className='logo mx-1' src={logo}></img>PechKas</NavLink>
              <button className=" navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>

              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto">
                  <li className="nav-item active">
                    <NavLink activeClassName='menu_active' exact className="nav-link active" to="/">Home </NavLink>
                  </li>
                  <li className="nav-item active">
                    <NavLink activeClassName='menu_active' exact className="nav-link active"  to="/service">Services</NavLink>
                  </li>
                  <li className="nav-item active">
                    <NavLink activeClassName='menu_active' exact className="nav-link active" to="/about">About</NavLink>
                  </li>
                  <li className="nav-item active">
                    <NavLink activeClassName='menu_active'exact className="nav-link active" to="/loginpro">Register as Professionals</NavLink>
                  </li>
                  <li className="nav-item active">
                    <NavLink activeClassName='menu_active'exact className="nav-link active" to="/login">Login/signup</NavLink>
                  </li>
                </ul>
          </div>
      </nav>
      </div> 
      </div> 
      </div> 
         
    </>

  )
}
export default Navbar;

